@charset "UTF-8";
@import url(//fonts.googleapis.com/earlyaccess/notosanskr.css);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before, blockquote:after {
  content: '';
  content: none; }

q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

button {
  font-size: 100%;
  font: inherit;
  border-radius: 0;
  outline: 0;
  color: inherit;
  cursor: pointer; }

* {
  box-sizing: border-box; }

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%; }

body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-family: 'Roboto', -apple-system, BlinkMacSystemFont, 'Apple SD Gothic Neo', '애플 SD 산돌고딕 Neo', 'Noto Sans KR', sans-serif;
  color: #222;
  background: #fff;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased; }

a {
  font-size: 14px;
  color: #222;
  outline: none;
  text-decoration: none; }

b,
strong {
  font-weight: 700; }

button {
  font-size: 14px;
  border: 0;
  outline: none;
  cursor: pointer; }
  button:disabled {
    cursor: default; }

.mobile-show {
  display: none !important; }
  @media screen and (max-width: 767px) {
    .mobile-show {
      display: block !important; } }

.mobile-hide {
  display: block !important; }
  @media screen and (max-width: 767px) {
    .mobile-hide {
      display: none !important; } }

.Toastify__toast-body {
  font-size: 14px; }

.slick-initialized .slick-slide {
  float: none;
  display: inline-block !important;
  vertical-align: middle !important; }

.slick-prev:before,
.slick-next:before {
  font-size: 28px; }

.slick-dots {
  bottom: 12px; }

.slick-dots li {
  margin: 0 2px; }

.slick-dots li,
.slick-dots li button,
.slick-dots li button:before {
  width: 10px;
  height: 10px;
  line-height: 10px; }
